<template>
    <div class="wildfire-widget">
        <div class="left-col">
            <div class="widget-slider-label">Room Temperature: 20°</div>
            <div class="compass-container">
                <!-- <div class="compass-label">{{ room-temp }}°</div> -->
                <!-- <round-slider
                    v-model="windDirection"
                    v-bind:tooltipFormat="tooltipText"
                    min="0"
                    max="359"
                    width="5"
                    startValue="windDirection"
                    start-angle="90"
                    line-cap="round"
                    radius="30"
                    handleSize="+10"
                    showTooltip="false"
                    rangeColor="#ADADAD"
                    pathColor="#ADADAD"
                    handleColor="#515151"
                    @input="logInteractionData('round slider', 'wind direction', $event)"
                /> -->
            </div>
        </div>
        <div class="right-col">
            <div class="speed-container">
                <div class="widget-slider-label">Bottom Temperature</div>
                <div class="speed-widget-label">{{ bottomTemp }} °C</div>
            </div>
            
            <input v-model="bottomTemp" v-on:input="onTempUpdate" @change="logInteractionData('slider', 'bottom temperature', $event.target.value)" type="range" min="20" max="80" value="70" step="1" class="widget-slider" id="bottom-temp">
          <!--
            <div class="temperature-container">
                <label class="widget-checkbox">
                    <input v-model="temperature" type="checkbox" id="temperature"/>
                    <div class="checkmark"></div>
                </label>
                <div class="widget-checkbox-label">Temperature</div>
            </div>
             <div class="side-view-container">
                <label class="widget-checkbox">
                    <input v-model="sideView" type="checkbox" id="side-view"/>
                    <div class="checkmark"></div>
                </label>
                <div class="widget-checkbox-label">Side View</div>
            </div>
          -->
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
// import RoundSlider from 'vue-round-slider';
export default {
    name: 'heatWidgetComponent',
    props: ['roomTemp'],
    components:{
        // RoundSlider
    },
    data(){
        return{
            temperature: false,
            sideView: false,
            windDirection: 90,
            bottomTemp: 70,
        }
    },
    mounted(){
        this.onTempUpdate();
    },
    methods:{
        logInteractionData(widgetType, variableName, value){
          let dataLog = {
            component_type: "widget",
            component_data: {
              widget_type: widgetType,
              variable_name: variableName,
              value: value,
            }
          }
          let logString = JSON.stringify(dataLog)
          if (window.fv && window["__USER_ID__"]) {
            window.fv.log(logString)
          }
        },
        onTempUpdate(){
            const percent = (this.bottomTemp - ($("#bottom-temp")[0].min)) / ($("#bottom-temp")[0].max-$("#bottom-temp")[0].min) * 100;
            $("#bottom-temp").css('background','linear-gradient(to right, #FF0A01 0%, #FF0A01 ' + percent + '%, #ADADAD ' + percent + '%, #ADADAD 100%)');
        },
        tooltipText(e){
            return e.value + '°'
        }

    },
    watch: {
        temperature: function(){
            this.$emit('sendValue', {type:'fv-checkbox', name:'temperature', value: this.temperature});
        },
        sideView: function(){
            this.$emit('sendValue', {type:'fv-checkbox', name:'side-view', value: this.sideView});
        },
        bottomTemp: function(){
            this.$emit('sendValue', {type:'fv-slider', name:'bottom-temp', value: this.bottomTemp});
        },
        windDirection: function(){
            this.$emit('sendValue', {type:'fv-slider', name:'wind-direction', value: this.windDirection});
        },
    }
}
</script>

